import type { IConfig } from "../config"
import Services from "../services"

class WebSocketService {
    services: Services
    config: IConfig["socket"]
    socket?: WebSocket
  
    constructor(services: Services, config: IConfig["socket"] = {} as IConfig["socket"]) {
      this.services = services;
      this.config = config;
    }
  
    /**
     *  WebSocket 
     * @param url  WebSocket 
     * @param protocols ,  
     */
    connect(url: string): void {
      const fullUrl = this.getWebSocketUrl(url);
      this.socket = new WebSocket(fullUrl);
    }
  
    /**
     *    WebSocket 
     * @param data   
     */
    send(method: string, payload: Record<string, string>): void {
        const data = JSON.stringify({
            method,
            payload: {
              ...payload
            }
          })

      if (this.socket) {
        this.socket.send(data);
      } else {
        console.error("WebSocket connection is not open.");
      }
    }
  
    /**
     *  WebSocket 
     * @param code  
     * @param reason  
     */
    close(code?: number, reason?: string): void {
      if (this.socket && this.socket.readyState === WebSocket.OPEN) {
        this.socket.close()
      }
    }
  
    /**
     *   URL  WebSocket 
     * @param url  WebSocket 
     * @returns  URL
     */
    private getWebSocketUrl(url: string): string {
      if (!url.match(/^(ws|wss|\/\/)/)) {
        return (this.config.baseUrl || "ws://") + url;
      }
      return url;
    }
  }

  export default WebSocketService;