import Tool from "../tool"
// import { IRectangle } from "@src/store/canvas/types"
import { IRectangle } from "@src/store/canvas/types"

class Rectangle extends Tool {
    canvas!: HTMLCanvasElement | null //    canvas,    
    mouseDown!: boolean // , ,    
    startX!: number
    startY!: number
    saved: any
    rect: any
    figures: IRectangle | null

    constructor(canvas: HTMLCanvasElement | null) {
        super(canvas) //    
        this.listen() //     
        this.rect = null
        this.figures = null
    }
    
    listen() {
       if(this.canvas) {
        //     ,     

        //  .bind(this)    ,   (this)    
        //      Rectangle,    ,     , 
        //        .
        this.canvas.onmousemove = this.mouseMoveHandler.bind(this)
        this.canvas.onmousedown = this.mouseDownHandler.bind(this)
        this.canvas.onmouseup = this.mouseUpHandler.bind(this)
       }
    }

    //     
    mouseUpHandler(e: MouseEvent) {
        this.mouseDown = false
        const allFigures = this.allFiguresTool as any
        this.allFiguresTool = [...allFigures, this.figures]
    }

    //     
    mouseDownHandler(e: MouseEvent) {
        this.mouseDown = true //       true
        if(this.ctx && e.target instanceof HTMLElement) { //           HTML
          this.ctx.beginPath() //    
          const rect = this.canvas!.getBoundingClientRect() //     
        
          //     x   y
          this.startX = e.clientX - rect.left
          this.startY = e.clientY - rect.top

          //    canvas
          this.saved = this.canvas!.toDataURL()
        }
    }

    //    
    mouseMoveHandler(e: MouseEvent) {
        if (this.mouseDown  && e.target instanceof HTMLElement) {
            const rect = this.canvas!.getBoundingClientRect()
            let currentX = e.clientX - rect.left
            let currentY = e.clientY - rect.top
            let width = currentX - this.startX
            let height = currentY - this.startY
            this.figures = {
                type: 'rectangle', 
                x: this.startX, 
                y: this.startY, 
                width: width, 
                height:height
            }
            this.draw(this.startX, this.startY, width, height) //    
        }
    }

    //   
   draw(x: number, y: number, w: number, h: number) {
       const img = new Image()
       img.src = this.saved
      
       //    onload   ,   
      img.onload = () => {
        this.ctx!.clearRect(0, 0, this.canvas!.width, this.canvas!.height) //  canvas
        this.ctx!.drawImage(img, 0, 0, this.canvas!.width, this.canvas!.height) //  ,   
        this.ctx!.beginPath() //    
        this.ctx!.rect(x, y, w, h)
        this.ctx!.fill() //  
        this.ctx!.stroke() //  
       }
    }
}

export default Rectangle