import { ICircle, IRectangle, ILine, IFreeDraw } from "@src/store/canvas/types"

interface AnimationParams {
    update: (params: AnimationParamsObject) => void
    render: (params: AnimationParamsObject) => void
    clear: () => void
}

interface AnimationParamsObject {
    diff: number
    timestamp: number
    pTimestamp: number
    fps: number
    secondPart: number
}

class Tool {
  canvas: HTMLCanvasElement | null
  ctx: CanvasRenderingContext2D | null
  allFiguresTool: (ICircle | IRectangle | ILine | IFreeDraw)[] | null
  animationId: any

  constructor(canvas: HTMLCanvasElement | null) {
    this.canvas = canvas
    this.ctx = canvas ? canvas.getContext("2d") : null
    this.allFiguresTool = []
    this.destroyEvents()
    this.animationId
  }

  set fillColor(color: string | CanvasGradient | CanvasPattern) {
    this.ctx && (this.ctx.fillStyle = color)
  }
  set strokeColor(color: string | CanvasGradient | CanvasPattern) {
    this.ctx && (this.ctx.strokeStyle = color)
  }

  set lineWidth(width: number) {
    this.ctx && (this.ctx.lineWidth = width)
  }

  destroyEvents() {
    if (this.canvas) {
      this.canvas.onmousemove = null
      this.canvas.onmousedown = null
      this.canvas.onmouseup = null
    }
  }

  animation(params: AnimationParams) { //   animation   params
	const { update, render, clear } = params //   params    update, render  clear
	let pTimestamp = 0 //   pTimestamp,      

	const tick = (timestamp: number) => { //   tick   timestamp
		this.animationId = requestAnimationFrame(tick) //    requestAnimationFrame   tick   
        console.log('timestamp', timestamp)

		const diff = timestamp - pTimestamp //        
		const fps = 1000 / diff //      (FPS)    
		const secondPart = diff / 1000 //   ,    
		pTimestamp = timestamp //     

		const params = { //   params    ,  , FPS   
			diff,
			timestamp,
			pTimestamp,
			fps,
			secondPart,
		};

		update(params) //   update   params    
		clear() //   clear         
		render(params) //   render   params     
	}

    requestAnimationFrame(tick) //   requestAnimationFrame   tick   
}
}

export default Tool
