import StoreModule from "../module"
import { ICountriesInitState, IApiResponseCountries, Iountry } from "./types"

/**
 *  
 */
class CountriesState extends StoreModule<ICountriesInitState> {

  /**
   *  
   * @return {Object}
   */
  initState(): ICountriesInitState {
    return {
      list: [],
      waiting: false
    };
  }

  /**
   *   
   */
  async load() {
    this.setState({...this.getState(), waiting: true}, '  ');

    const res: IApiResponseCountries = await this.services.api.request({
      url: `/api/v1/countries?lang=ru&limit=228&skip=0&fields=%2A`
    })
    //    
    this.setState({
      ...this.getState(),
      list: res.data.result.items,
      waiting: false
    }, '  ');
  }

  async search(query: string) {
    this.setState(
      { ...this.getState(), waiting: true },
      "  "
    );

    const res: IApiResponseCountries = await this.services.api.request({
      url: `/api/v1/countries?search[query]=${query}&fields=items(_id,title,code),count&limit=*`,
    });

    if (res.status === 200) {
      this.setState(
        {
          ...this.getState(),
          list: res.data.result.items,
          waiting: false,
        },
        " "
      );
    }
  }

  /**
   *   
   * @param id
   */
  selectountry(id: string) {
    this.setState({
        ...this.getState(),
      list: this.getState().list.map((item: Iountry) => {
        if (item._id === id) {
          //  
          return {
            ...item,
            selected: !item.selected
          }
        }
        // return item.selected ? {...item, selected: false} : item;
        return item
      })
    })
  }
}

export default CountriesState