import { IFreeDraw } from "@src/store/canvas/types"
import Tool from "../tool"

class Brush extends Tool {
    canvas!: HTMLCanvasElement | null //    canvas,    
    mouseDown!: boolean // , ,    
    figures: IFreeDraw | null

    constructor(canvas: HTMLCanvasElement | null) {
        super(canvas) //    
        this.listen() //     
        this.figures = null
    }
    
    listen() {
       if(this.canvas) {
        //     ,     

        //  .bind(this)    ,   (this)    
        //      Brush,    ,     , 
        //        .
        this.canvas.onmousemove = this.mouseMoveHandler.bind(this)
        this.canvas.onmousedown = this.mouseDownHandler.bind(this)
        this.canvas.onmouseup = this.mouseUpHandler.bind(this)
       }
    }

    //     
    mouseUpHandler(e: MouseEvent) {
        this.mouseDown = false
        const allFigures = this.allFiguresTool as any
        this.allFiguresTool = [...allFigures, this.figures]
    }

    //     
    mouseDownHandler(e: MouseEvent) {
        this.mouseDown = true //       true
        if(this.ctx && e.target instanceof HTMLElement) { //           HTML
            this.ctx.beginPath() //    
            const rect = this.canvas!.getBoundingClientRect() //     
            this.ctx.moveTo(e.clientX - rect.left, e.clientY - rect.top) //  clientX  clientY  
            const startPoint = { x: e.clientX - rect.left, y: e.clientY - rect.top }
            this.figures = { type: 'freeDraw', points: [startPoint] }
            // e.target && this.ctx.moveTo(e.pageX - e.target.offsetLeft, e.pageY - e.target.offsetTop)
        }
    }

    //    
    mouseMoveHandler(e: MouseEvent) {
        if (this.mouseDown  && e.target instanceof HTMLElement) {
            const rect = this.canvas!.getBoundingClientRect()
            const currentPoint = { x: e.clientX - rect.left, y: e.clientY - rect.top }
            this.figures && this.figures.points.push(currentPoint) //      
            this.draw(e.clientX - rect.left, e.clientY - rect.top) //    
            // this.draw(e.pageX - e.target.offsetLeft, e.pageY - e.target.offsetTop) 
        }
    }

    //   
   draw(x: number, y: number) {
    if(this.ctx) { //    
      this.ctx.lineTo(x, y) //     
      this.ctx.stroke() //  
    }
}
}

export default Brush